  methods: {
    changeMod(e) {
      let target = e.currentTarget;
      $(target)
        .parent()
        .children()
        .removeClass("selected");
      $(target).addClass("selected");
      let mod = target.getAttribute("for");
      let scrollHeight = 0;
      var preDom = $("." + mod)
        .parent()
        .children();
      for (let i = 0; i < preDom.length; i++) {
        if (preDom[i].className === mod) {
          break;
        }
        scrollHeight = scrollHeight + $(preDom[i]).height();
      }

      $(".content_right").scrollTop(scrollHeight);
    }
  }
};
